<template lang="html">
  <div id="issue">
    <div style="margin: 15px 15px 150px;">
      <div class="box tableTop">
        <div class="box-header with-border">
          <h3 class="box-title">分配二维码</h3>
        </div>
        <div class="">
          <div class="table-responsive">
            <el-row style="margin-bottom: 15px" type="flex" class="row-bg" justify="center">
              <el-col :span="4">
                <div class="alignRight" style="line-height: 42px">产品:</div>
              </el-col>
              <el-col :span="6">
                <div class="grid-content bg-purple-light">
                  <el-radio class="radio" v-model="query.sysType" label="hss">好收收</el-radio>
                  <el-radio class="radio" v-model="query.sysType" label="hsy">好收银</el-radio>
                </div>
              </el-col>
              <el-col :span="8">
                <div class="grid-content bg-purple-light"></div>
              </el-col>
            </el-row>
            <el-row type="flex" class="row-bg" justify="center">
              <el-col :span="4">
                <div class="alignRight">代理名称:</div>
              </el-col>
              <el-col :span="6">
                <div class="grid-content bg-purple-light">
                  <input @keyup="find()" class="inp" v-model="query.name" placeholder="输入代理手机号或名称">
                  <div style="position: relative">
                    <ul class="col-sm-12 list" v-if="listIsShow">
                      <li v-for="(findDealer,index) in this.$data.findDealers" @click='handleSelect(index)'>
                        <span>{{findDealer.name}}</span>
                        <span>{{findDealer.mobile}}</span>
                      </li>
                    </ul>
                  </div>
                </div>
              </el-col>
              <el-col :span="8">
                <div class="grid-content bg-purple-light"></div>
              </el-col>
            </el-row>
            <el-row style="margin-bottom: 15px" type="flex" class="row-bg" justify="center">
              <el-col :span="4">
                <div class="alignRight" style="line-height: 42px">类型:</div>
              </el-col>
              <el-col :span="6">
                <div class="grid-content bg-purple-light">
                  <el-radio class="radio" v-model="query.a" label="1">实体码</el-radio>
                  <el-radio class="radio" v-model="query.a" label="2">电子码</el-radio>
                </div>
                <div>未分配二维码：98个</div>
              </el-col>
              <el-col :span="8">
                <div class="grid-content bg-purple-light"></div>
              </el-col>
            </el-row>
            <el-row style="margin-bottom: 15px" type="flex" class="row-bg" justify="center">
              <el-col :span="4">
                <div class="alignRight" style="line-height: 42px">分配方式:</div>
              </el-col>
              <el-col :span="6">
                <div class="grid-content bg-purple-light">
                  <el-radio class="radio" v-model="query.b" label="1">按码段</el-radio>
                  <el-radio class="radio" v-model="query.b" label="2">按个数</el-radio>
                </div>
              </el-col>
              <el-col :span="8">
                <div class="grid-content bg-purple-light"></div>
              </el-col>
            </el-row>
            <el-row style="margin-bottom: 15px" type="flex" class="row-bg" justify="center" v-if="query.b==1">
              <el-col :span="4">
                <div class="alignRight">起始码:</div>
              </el-col>
              <el-col :span="6">
                <div class="grid-content bg-purple-light">
                  <el-input size="small" v-model="query.startCode" placeholder="请输入二维码号"></el-input>
                </div>
              </el-col>
              <el-col :span="8">
                <div class="grid-content bg-purple-light"></div>
              </el-col>
            </el-row>
            <el-row style="margin-bottom: 15px" type="flex" class="row-bg" justify="center" v-if="query.b==1">
              <el-col :span="4">
                <div class="alignRight">终止码:</div>
              </el-col>
              <el-col :span="6">
                <div class="grid-content bg-purple-light">
                  <el-input size="small" v-model="query.endCode" placeholder="请输入二维码号"></el-input>
                </div>
              </el-col>
              <el-col :span="8">
                <div class="grid-content bg-purple-light"></div>
              </el-col>
            </el-row>
            <el-row style="margin-bottom: 15px" type="flex" class="row-bg" justify="center" v-if="query.b==2">
              <el-col :span="4">
                <div class="alignRight">个数:</div>
              </el-col>
              <el-col :span="6">
                <div class="grid-content bg-purple-light">
                  <el-input size="small" v-model="query.number" placeholder="请输入分配个数"></el-input>
                </div>
              </el-col>
              <el-col :span="8">
                <div class="grid-content bg-purple-light"></div>
              </el-col>
            </el-row>
          </div>
        </div>
        <el-row type="flex" class="row-bg" justify="center">
          <el-col :span="4">
            <div class="alignRight"></div>
          </el-col>
          <el-col :span="6">
            <div class="grid-content bg-purple-light" style="width: 100%">
              <div class="btn btn-primary" @click="create" style="width: 100%;float: right;margin: 20px 0 100px;">
                立即分配
              </div>
            </div>
          </el-col>
          <el-col :span="8">
            <div class="grid-content bg-purple-light"></div>
          </el-col>
        </el-row>
        <!--审核-->
        <div v-if="isShow">
          <el-dialog :show-close="false" title="分配成功" v-model="isShow">
            <div class="maskCon">
              <span>商户名称：</span>
              <!--<span>{{records[this.$data.index].merchantName}}</span>-->
            </div>
            <div class="maskCon">
              <span>代理商手机号：</span>
              <!--<span>{{records[index].merchantNo}}</span>-->
            </div>
            <div class="maskCon">
              <span>分配时间：</span>
              <!--<span>{{records[index].settleAmount}}</span>-->
            </div>
            <div class="maskCon">
              <span>类型：</span>
              <!--<span>{{records[index].tradeNumber}}笔</span>-->
            </div>
            <div class="maskCon">
              <span>分配个数：</span>
              <!--<span>{{records[index].tradeDate}}</span>-->
            </div>
            <div class="maskCon">
              <span>分配号段：</span>
              <!--<span>{{records[index].tradeDate}}</span>-->
            </div>
            <div slot="footer" class="dialog-footer" style="text-align: center;">
              <el-button @click="isShow = false">返回</el-button>
              <!--<el-button @click="settle(2,records[index].id)">结算已对账部分</el-button>-->
              <!--<el-button @click="settle(3,records[index].id)">强制结算全部</el-button>-->
            </div>
          </el-dialog>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="babel">
  export default {
    name: 'issue',
    data () {
      return {
        query: {
          sysType:'',
          a:'',
          b:1,
          mobile: '',
          name: '',
          dealerId:'',
          loginName:'',
          loginPwd:'',
          email:'',
          belongProvinceCode:'',
          belongProvinceName:'',
          belongCityCode: '',
          belongCityName: '',
          belongArea: '',
          bankCard: '',
          bankAccountName: '',
          bankReserveMobile: '',
          idCard: '',
        },
        listIsShow:false,
        findDealers:'',
        isShow:false
      }
    },
    created: function () {

    },
    watch: {

    },
    methods: {
      find: function () {
        if(this.$data.query.name!=''){
          this.$http.post('/admin/dealer/find',{condition:this.$data.query.name})
            .then(function(res){
              this.$data.findDealers=res.data;
              if(this.$data.findDealers.length!=0){
                this.$data.listIsShow = true;
              }else {
                this.$data.listIsShow = false;
              }
            },function(err){
              this.$message({
                showClose: true,
                message: err.statusMessage,
                type: 'error'
              });
            })
        }else{
          this.$data.listIsShow = false;
        }
      },
      //选中代理时
      handleSelect(index) {
        this.$data.query.name = this.$data.findDealers[index].name;
        this.$data.query.dealerId = this.$data.findDealers[index].dealerId;
        this.$data.listIsShow = false;
      },
      //创建
      create: function () {
          console.log(this.$data.query)
        /*this.$http.post('/admin/user/addFirstDealer2', this.$data.query)
          .then(function (res) {
            this.$message({
              showClose: true,
              message: '创建成功',
              type: 'success'
            });
            this.$router.push('/admin/record/agentListFir')
          }, function (err) {
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            });
          })*/
      },
      goBack: function () {
        if(this.$route.query.level==2){
          this.$router.push('/admin/record/agentListSec')
        }else {
          this.$router.push('/admin/record/agentListFir')
        }
      },
    },
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .alignRight {
    margin-right: 15px;
    text-align: right;
    height: 30px;
    line-height: 30px;
    font-weight: bold;
    margin-bottom: 10px;
  }

  ul {
    list-style-type: none;
    background: #fff;
    border: 1px solid #d0d0d0;
    padding: 5px 0;
    position: absolute;
    z-index: 10;
    overflow: auto;
    max-height: 115px;
  }

  li {
    padding: 5px 10px;
  }
  li:hover{
    background: #3ea0d8;
  }
  .inp{
    height: 30px;
    background-color: #fff;
    border-radius: 4px;
    border: 1px solid #bfcbd9;
    color: #1f2d3d;
    display: block;
    padding: 3px 10px;
    transition: border-color .2s cubic-bezier(.645,.045,.355,1);
    width: 100%;
  }
  .inp:focus {
    outline: 0;
    border-color: #20a0ff;
  }
  .maskCon{
    margin:0 0 15px 50px
  }
</style>



